:host {
  @apply block h-full w-full;

  ::ng-deep .adaptive-layout {
    @apply h-full w-full flex flex-col;

    // Ensure PrimeFaces splitter takes full height
    .p-splitter {
      @apply flex-1 h-full min-h-full;
    }

    // Fix for panel content
    .p-splitter-panel > div {
      @apply h-full;
    }
  }

  &.has-hidden-panel {
    ::ng-deep {
      .zero-width-panel {
        @apply max-w-0 min-w-0 w-0 p-0 m-0 border-0 overflow-hidden pointer-events-none;
        flex: 0 0 0 !important;
      }

      // Hide gutter after a zero-width panel
      .zero-width-panel + .p-splitter-gutter {
        @apply hidden;
      }

      // First panel taking full space when other panels are hidden
      .p-splitter-panel:first-child:not(.zero-width-panel) {
        @apply flex-1 w-full;
      }
    }
  }
}
